.profileScreen {
  height: 100vh;
  color: #fff;
}

.profileScreen__body {
  display: flex;
  flex-direction: column;
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 8%;
  max-width: 800px;
}

.profileScreen__body > h1 {
  font-size: 60px;
  font-weight: 400px;
  border-bottom: 1px solid #2b2c2d;
  margin-bottom: 20px;
}

.profileScreen__info {
  display: flex;
}

.profileScreen__info > img {
  height: 100px;
}

.profileScreen__details {
  color: #fff;
  margin-left: 25px;
  flex: 1;
}

.profileScreen__details > h2 {
  background-color: gray;
  padding: 15px;
  font-size: 15px;
  padding-left: 20px;
}

.profileScreen__signOut {
  padding: 10px 20px;
  font-size: 1rem;
  margin-top: 5px;
  width: 100%;
  color: #fff;
  background-color: #e50914;
  border: none;
  cursor: pointer;
  font-weight: 600;
}

.profileScreen__plans {
  margin-top: 20px;
}

.profileScreen__plans > h3 {
  border-bottom: 1px solid #2b2c2d;
  padding-bottom: 10px;
}

.profileScreen__plans > p {
  padding: 10px 0;
}

.profileScreen__plans > h4 {
  margin-bottom: 20px;
}

.profileScreen__plans > button {
  float: right;
  padding: 10px 20px;
  background-color: #e50914;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  text-align: center;
  transition: 0.5s;
}

.profileScreen__plans > button:hover {
  background-color: white;
  color: #e50915;
}

.profileScreen__plans > .profileScreen__currentPlan {
  background-color: gray;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
}

.profileScreen__plans > .profileScreen__currentPlan:hover {
  opacity: 0.8 !important;
}
